// RecruiteeSignup.js
import React, { useState } from 'react';

function RecruiteeSignup() {
    const [formData, setFormData] = useState({
        username: '',
        password: '',
        age: '',
        ethnicity: '',
        height: '',
    });

    const handleChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value,
        });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        fetch('/api/signup/recruitee/', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                user: {
                    username: formData.username,
                    password: formData.password,
                },
                age: formData.age,
                ethnicity: formData.ethnicity,
                height: formData.height,
            }),
        })
        .then((response) => {
            if (response.ok) {
                return response.json();
            }
            return Promise.reject(response);
        })
        .then((data) => {
            console.log(data);
            // Handle success
        })
        .catch((error) => {
            console.error('Error:', error);
            // Handle errors here
        });
    };

    return (
        <div className="min-h-screen bg-gray-100 flex flex-col justify-center">
            <div className="max-w-md w-full mx-auto">
                <div className="bg-white p-8 border border-gray-300 rounded-lg shadow-lg">
                    <form className="space-y-6" onSubmit={handleSubmit}>
                        <div>
                            <label className="text-sm font-bold text-gray-600 block">Username</label>
                            <input
                                name="username"
                                type="text"
                                value={formData.username}
                                onChange={handleChange}
                                className="w-full p-2 border border-gray-300 rounded mt-1"
                                placeholder="Username"
                                required
                            />
                        </div>
                        <div>
                            <label className="text-sm font-bold text-gray-600 block">Password</label>
                            <input
                                name="password"
                                type="password"
                                value={formData.password}
                                onChange={handleChange}
                                className="w-full p-2 border border-gray-300 rounded mt-1"
                                placeholder="Password"
                                required
                            />
                        </div>
                        <div>
                            <label className="text-sm font-bold text-gray-600 block">Age</label>
                            <input
                                name="age"
                                type="number"
                                value={formData.age}
                                onChange={handleChange}
                                className="w-full p-2 border border-gray-300 rounded mt-1"
                                placeholder="Age"
                                required
                            />
                        </div>
                        <div>
                            <label className="text-sm font-bold text-gray-600 block">Ethnicity</label>
                            <input
                                name="ethnicity"
                                type="text"
                                value={formData.ethnicity}
                                onChange={handleChange}
                                className="w-full p-2 border border-gray-300 rounded mt-1"
                                placeholder="Ethnicity"
                                required
                            />
                        </div>
                        <div>
                            <label className="text-sm font-bold text-gray-600 block">Height</label>
                            <input
                                name="height"
                                type="number"
                                value={formData.height}
                                onChange={handleChange}
                                className="w-full p-2 border border-gray-300 rounded mt-1"
                                placeholder="Height in cm"
                                required
                            />
                        </div>
                        <button
                            type="submit"
                            className="w-full py-2 px-4 bg-blue-500 hover:bg-blue-700 rounded-md text-white text-sm font-bold"
                        >
                            Sign Up
                        </button>
                    </form>
                </div>
            </div>
        </div>
    );
}

export default RecruiteeSignup;
